.chip {
  font-family: inherit;
  line-height: 1.1;
  color: inherit;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #525252;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  color: #21174b;
  padding: 8px 12px;
  background-color: #ffffff;
  border: 1px solid #21174b;
  border-radius: 20px;
  margin: 0;
  cursor: pointer;
  text-align: left;
  font-size: 13px;
  font-weight: 400;
}
.chip small,
.chip .small {
  font-weight: 400;
  line-height: 1;
  color: #777777;
}
.chip small,
.chip .small {
  font-size: 75%;
}
.chip:focus {
  outline: none;
}
.chip:focus-visible,
.chip:global(.focus) {
  background: rgba(48, 48, 48, 0.1);
}
.chip:hover,
.chip:global(.hover) {
  box-shadow: 0px 1px 3.24px 0.76px rgba(0, 0, 0, 0.08);
}
.disabled[disabled] {
  opacity: 0.6;
  cursor: not-allowed;
}
.disabled[disabled]:hover {
  box-shadow: none;
}
.noCursor {
  cursor: auto;
}
.selected {
  color: #ffffff;
  background-color: #21174b;
}
.selected:focus-visible {
  color: #303030;
}
.icon {
  fill: #ffffff;
}
.icon:focus {
  outline: none;
}
.dismissIcon {
  fill: #ffffff;
  margin-left: 8px;
  height: 8px;
  width: 8px;
}
.dismissIcon:focus {
  outline: none;
}
.skeleton {
  font-family: inherit;
  line-height: 1.1;
  color: inherit;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #525252;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  color: #21174b;
  padding: 8px 12px;
  background-color: #ffffff;
  border: 1px solid #21174b;
  border-radius: 20px;
  margin: 0;
  cursor: pointer;
  text-align: left;
  font-size: 13px;
  font-weight: 400;
  min-width: 58px;
  background-color: #f7f7f7;
  border: none;
  cursor: auto;
  display: inline-block;
}
.skeleton small,
.skeleton .small {
  font-weight: 400;
  line-height: 1;
  color: #777777;
}
.skeleton small,
.skeleton .small {
  font-size: 75%;
}
.skeleton:focus {
  outline: none;
}
.skeleton:focus-visible,
.skeleton:global(.focus) {
  background: rgba(48, 48, 48, 0.1);
}
.skeleton:hover,
.skeleton:global(.hover) {
  box-shadow: 0px 1px 3.24px 0.76px rgba(0, 0, 0, 0.08);
}
.skeleton:hover {
  box-shadow: none;
}
.skeleton .animation {
  line-height: inherit;
}
/**
 * SIZE: small
 */
.small {
  padding: 4px 8px;
}
